"use client";

import Link from 'next/link';
import { usePathname } from 'next/navigation';

export default function Navbar() {
    const pathname = usePathname();

    // Helper function to style active links
    const linkStyle = (path) => ({
        textDecoration: 'none',
        fontSize: '0.9rem',
        fontWeight: pathname === path ? '700' : '400',
        color: pathname === path ? '#fff' : 'var(--subtle-text-color)',
        transition: 'color 0.3s ease',
        borderBottom: pathname === path ? '2px solid var(--accent-color-1)' : 'none',
        paddingBottom: '5px'
    });

    return (
        <nav style={{
            position: 'fixed',
            top: 0,
            width: '100%',
            zIndex: 1000,
            padding: '15px 0',
            backdropFilter: 'blur(12px)',
            WebkitBackdropFilter: 'blur(12px)',
            backgroundColor: 'rgba(12, 12, 14, 0.7)',
            borderBottom: '1px solid var(--border-color)'
        }}>
            <div style={{
                maxWidth: '1000px',
                margin: '0 auto',
                padding: '0 20px',
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center'
            }}>
                {/* Brand Logo */}
                <Link href="/" style={{
                    fontWeight: 900,
                    fontSize: '1.3rem',
                    textDecoration: 'none',
                    color: '#fff',
                    letterSpacing: '-1px'
                }}>
                    ROHAN<span style={{ color: 'var(--accent-color-1)' }}>RAJE</span>
                </Link>

                {/* Navigation Links */}
                <div style={{ display: 'flex', gap: '20px', alignItems: 'center' }}>
                    <Link href="/about" style={linkStyle('/about')}>
                        About
                    </Link>
                    <Link href="/experience" style={linkStyle('/experience')}>
                        Experience
                    </Link>
                    <Link href="/projects" style={linkStyle('/projects')}>
                        Projects
                    </Link>
                    <Link href="/education" style={linkStyle('/education')}>
                        Education
                    </Link>

                    {/* Resume Button */}
                    <a
                        href="/rohanraje.pdf"
                        target="_blank"
                        style={{
                            textDecoration: 'none',
                            backgroundColor: 'var(--accent-color-1)',
                            color: '#fff',
                            padding: '8px 16px',
                            borderRadius: '6px',
                            fontSize: '0.85rem',
                            fontWeight: '600',
                            marginLeft: '10px'
                        }}
                    >
                        Resume
                    </a>
                </div>
            </div>
        </nav>
    );
}